                        –ö–∞—Ç–∞–ª–æ–≥
                    </router-link>
                </li>
                <li class="breadcrumbs__item">
                    <router-link
                        class="breadcrumbs__link"
                        :to="{ name: 'basket' }"
                    >
                        –ö–∞—Ç–∞–ª–æ–≥
                    </router-link>
                </li>
                <li class="breadcrumbs__item">
                    <a class="breadcrumbs__link"> –û—Ñ–æ—Ä–º–ª–µ–Ω–∏–µ –∑–∞–∫–∞–∑–∞ </a>
                </li>
            </ul>

            <h1 class="content__title">–ö–æ—Ä–∑–∏–Ω–∞</h1>
            <span class="content__info"> {{ goodsArr.length }} —Ç–æ–≤–∞—Ä–∞ </span>
        </div>

        <section class="cart">
            <form class="cart__form form" @submit.prevent="sybmit">
                <div class="cart__field">
                    <div class="cart__data">
                        <infoFildsOrderText
                            v-model:value="dataGoods.name"
                            title="–§–ò–û"
                            placeholder="–í–≤–µ–¥–∏—Ç–µ –≤–∞—à–µ –ø–æ–ª–Ω–æ–µ –∏–º—è"
                            :error="errorOrder.name"
                        ></infoFildsOrderText>

                        <infoFildsOrderText
                            v-model:value="dataGoods.address"
                            title="–ê–¥—Ä–µ—Å –¥–æ—Å—Ç–∞–≤–∫–∏"
                            placeholder="–í–≤–µ–¥–∏—Ç–µ –≤–∞—à –∞–¥—Ä–µ—Å"
                            :error="errorOrder.address"
                        ></infoFildsOrderText>

                        <infoFildsOrderText
                            v-model:value="dataGoods.phone"
                            title="–¢–µ–ª–µ—Ñ–æ–Ω"
                            placeholder="–í–≤–µ–¥–∏—Ç–µ –≤–∞—à —Ç–µ–ª–µ—Ñ–æ–Ω"
                            :error="errorOrder.phone"
                            :type="'tel'"
                        ></infoFildsOrderText>

                        <infoFildsOrderText
                            v-model:value="dataGoods.email"
                            title="Email"
                            placeholder="–í–≤–µ–¥–∏ –≤–∞—à Email"
                            :error="errorOrder.email"
                            :type="'email'"
                        ></infoFildsOrderText>

                        <infoFildsOrderTextarea
                            v-model:value="dataGoods.comment"
                            title="–ö–æ–º–º–µ–Ω—Ç–∞—Ä–∏–π –∫ –∑–∞–∫–∞–∑—É"
                            placeholder="–í–∞—à–∏ –ø–æ–∂–µ–ª–∞–Ω–∏—è"
                        ></infoFildsOrderTextarea>
                    </div>

                    <div class="cart__options">
                        <h3 class="cart__title">–î–æ—Å—Ç–∞–≤–∫–∞</h3>
                        <ul class="cart__options options">
                            <li class="options__item">
                                <label class="options__label">
                                    <input
                                        class="options__radio sr-only"
                                        type="radio"
                                        name="delivery"
                                        value="0"
                                        checked=""
                                    />
                                    <span class="options__value">
                                        –°–∞–º–æ–≤—ã–≤–æ–∑ <b>–±–µ—Å–ø–ª–∞—Ç–Ω–æ</b>
                                    </span>
                                </label>
                            </li>
                            <li class="options__item">
                                <label class="options__label">
                                    <input
                                        class="options__radio sr-only"
                                        type="radio"
                                        name="delivery"
                                        value="500"
                                    />
                                    <span class="options__value">
                                        –ö—É—Ä—å–µ—Ä–æ–º <b>500 ‚ÇΩ</b>
                                    </span>
                                </label>
                            </li>
                        </ul>

                        <h3 class="cart__title">–û–ø–ª–∞—Ç–∞</h3>
                        <ul class="cart__options options">
                            <li class="options__item">
                                <label class="options__label">
                                    <input
                                        class="options__radio sr-only"
                                        type="radio"
                                        name="pay"
                                        value="card"
                                    />
                                    <span class="options__value">
                                        –ö–∞—Ä—Ç–æ–π –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏
                                    </span>
                                </label>
                            </li>
                            <li class="options__item">
                                <label class="options__label">
                                    <input
                                        class="options__radio sr-only"
                                        type="radio"
                                        name="pay"
                                        value="cash"
                                    />
                                    <span class="options__value">
                                        –ù–∞–ª–∏—á–Ω—ã–º–∏ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏
                                    </span>
                                </label>
                            </li>
                        </ul>
                    </div>
                </div>

                <div class="cart__block">
                    <ul class="cart__orders">
                        <li
                            class="cart__order"
                            v-for="item in goodsArr"
                            :key="item.product.id"
                        >
                            <h3>{{ item.product.title }}</h3>
                            <b
                                >{{
                                    $filters.numberFormat(
                                        item.product.price * item.quantity
                                    )
                                }}
                                ‚ÇΩ</b
                            >
                            <span>–ê—Ä—Ç–∏–∫—É–ª: {{ item.product.id }}</span>
                        </li>
                    </ul>

                    <div class="cart__total">
                        <p>–î–æ—Å—Ç–∞–≤–∫–∞: <b>500 ‚ÇΩ</b></p>
                        <p>
                            –ò—Ç–æ–≥: <b>{{ goodsArr.length }}</b> —Ç–æ–≤–∞—Ä–∞ –Ω–∞ —Å—É–º–º—É
                            <b>{{ $filters.numberFormat(fullPrice) }} ‚ÇΩ</b>
                        </p>
                    </div>

                    <button
                        class="cart__button button button--primery"
                        type="submit"
                    >
                        –û—Ñ–æ—Ä–º–∏—Ç—å –∑–∞–∫–∞–∑
                    </button>
                </div>
                <div class="cart__error form__error-block" v-if="errorMessage">
                    <h4>–ó–∞—è–≤–∫–∞ –Ω–µ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–∞!</h4>
                    <p>
                        {{ errorMessage }}
                    </p>
                </div>
            </form>
        </section>
    </main>

    <Footer></Footer>
</template>

<script>
import Header from "@/components/Header";
import Footer from "@/components/Footer";
import infoFildsOrderText from "@/components/infoFildsOrderText";
import infoFildsOrderTextarea from "@/components/infoFildsOrderTextarea";
import { mapState, mapGetters, mapActions, mapMutations } from "vuex";
import axios from "axios";
import { store_URL } from "@/axiosURL";

export default {
    components: {
        Header,
        Footer,
        infoFildsOrderText,
        infoFildsOrderTextarea,
    },
    data() {
        return {
            dataGoods: {},
            errorOrder: {},
            errorMessage: "",
        };
    },
    computed: {
        ...mapState({
            goodsArr: (state) => state.basket.basket,
            accessKey: (state) => state.basket.userAccessKey,
        }),

        fullPrice() {
            let fullPrice = 0;

            this.goodsArr.map((item) => {
                fullPrice += item.product.price * item.quantity;
            });

            return fullPrice;
        },
    },
    methods: {
        ...mapMutations({
            updateBasket: "basket/resetBasket",
            setOrederData: "basket/setOrederData",
        }),
        sybmit() {
            axios({
                method: "POST",
                url: store_URL + "orders",
                data: { ...this.dataGoods },
                params: {
                    userAccessKey: this.accessKey,
                },
            })
                .catch((er) => {
                    this.errorOrder = er.response.data.error.request || {};
                    this.errorMessage = er.response.data.error.message || "";
                })
                .then((response) => {
                    this.updateBasket([]);
                    this.setOrederData(response.data);
                    this.$router.push({
                        name: "orderStatus",
                        params: { id: response.data.id },
                    });
                });
        },
    },
};
</script>

